.login{
	position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #f6f5f5;
    font-size: 14px;
    text-align: center;
    .login_footer{
    	position: fixed;
    	bottom: 0;
    	left: 0;
    }
    .login-box{
    	border-radius: 7px;
    	min-height: 552px;
    	background: url(login_bg.jpg) no-repeat;
    	filter:"progid:DXImageTransform.Microsoft.AlphaImageLoader(sizingMethod='scale')";  
		-moz-background-size:50% 100%;  
		background-size:50% 100%;
    }
}
.login-con{
	background: #fff;
	min-height: 552px;
	font-size: 14px;
}
.login-logo{
	display: inline-block;
	margin-top: 50px;
	width: 80px;
	height: 80px;
	background: url(logo.jpg) no-repeat;
}
.login-tit{
	font-size: 28px;
	height: 52px;
	line-height: 52px;
}
.login-form{
	padding-top: 21px;
	.login-input{
		position: relative;
		width: 100%;
		margin-bottom: 10px;
		color: #ccc;
		input{
			width: 75%;
			border: 1px solid #ccc;
			border-radius: 40px;
			height: 40px;
			line-height: 40px;
			text-indent: 20px;
		}
		i{
			position: absolute;
			right: 18%;
    		top: 27%;
		}
	}
	.login-role{
		text-align: left;
		padding-left: 13%;
		width: 75%;
		line-height: 40px;
		color: #666;
		i{
			color: #ccc;
		}
	}
	.login-btn{
		margin-top: 10px;
		width: 66%;
		color: #666;
	}
	.login-reg{
		
	}
}